import React from "react";
import s from './post.module.css'
const Post = (props) => {
  return (
    <div className={s.post}>
      <img className={s.post__avatar} src={props.url}/>
      <p className={s.post__text} >{props.message}</p>
      <span className={s.post__like} >   <img className={s.post__imgLike} src="https://s1.iconbird.com/ico/2013/9/452/w512h4481380476860heart.png"/> {props.likeCount}</span>
    </div>
  )
}
export default Post