import React from "react";
import classes from "./dialog__item.module.css";
import {NavLink} from "react-router-dom";

const DialogItem = (props) =>{
  return(
    <div>
      <NavLink to={"/dialogs/" + props.id} className={classes.dialogs__item} activeClassName={classes.active}>{props.name}</NavLink>
    </div>
  )
}
export default DialogItem;