import React from 'react';
import classes from './navbar.module.css';
import {NavLink} from "react-router-dom";
const Navbar = () => {
  return (
    <nav className={classes.navbar}>
      <ul className={classes.navbar__menu}>
        <li><NavLink className={classes.navbar__menuLink} activeClassName={classes.active} to="/profile">Профиль</NavLink></li>
        <li><NavLink className={classes.navbar__menuLink} activeClassName={classes.active} to="/dialogs">Сообщения</NavLink></li>
        <li><NavLink className={classes.navbar__menuLink} activeClassName={classes.active} to="/news">Новости</NavLink></li>
        <li><NavLink className={classes.navbar__menuLink} activeClassName={classes.active} to="/music">Музыка</NavLink></li>
        <li><NavLink className={classes.navbar__menuLink} activeClassName={classes.active} to="/setting">Настройки</NavLink></li>
      </ul>
    </nav>
  )
}
export default Navbar;