import React from "react";
import Post from "./Post/Post.jsx";
import s from './myPosts.module.css'


const Myposts = (props) => {
  const postElements = props.state.posts.map(item => <Post message={item.message} likeCount={item.likeCount}
                                                           url={item.url}/>)

  return (
    <div>
      <h3>Мои посты</h3>
        <textarea></textarea>
        <button onClick={()=>{console.log('Бдыщ!')}}>Добавить пост</button>
      <div className={s.posts}>
        {postElements}
      </div>
    </div>
  )
}
export default Myposts;